import db from '@/lib/db';
import { NextResponse } from 'next/server';

export async function GET() {
    try {
        const transactions = db.prepare(`
      SELECT t.*, p.name as product_name, u.name as user_name 
      FROM transactions t
      JOIN products p ON t.product_id = p.id
      JOIN users u ON t.user_id = u.id
      ORDER BY t.timestamp DESC
    `).all();
        return NextResponse.json(transactions);
    } catch (error) {
        return NextResponse.json({ error: 'Failed to fetch transactions' }, { status: 500 });
    }
}

export async function POST(request) {
    try {
        const { product_id, user_id, type, quantity } = await request.json();

        if (!product_id || !user_id || !type || !quantity) {
            return NextResponse.json({ error: 'Missing required fields' }, { status: 400 });
        }

        // Start a transaction
        const transaction = db.transaction(() => {
            // Check product availability
            const product = db.prepare('SELECT quantity FROM products WHERE id = ?').get(product_id);

            if (!product) {
                throw new Error('Product not found');
            }

            let newQuantity = product.quantity;
            
            if (type === 'CHECKOUT') {
                if (product.quantity < quantity) {
                    throw new Error(`Insufficient quantity. Available: ${product.quantity}, Requested: ${quantity}`);
                }
                newQuantity -= quantity;
            } else if (type === 'CHECKIN') {
                // Check how much the user has checked out for this product
                const userCheckout = db.prepare(`
                    SELECT SUM(quantity) as total_checked_out FROM transactions 
                    WHERE user_id = ? AND product_id = ? AND type = 'CHECKOUT'
                `).get(user_id, product_id);

                const userCheckin = db.prepare(`
                    SELECT SUM(quantity) as total_checked_in FROM transactions 
                    WHERE user_id = ? AND product_id = ? AND type = 'CHECKIN'
                `).get(user_id, product_id);

                const totalCheckedOut = userCheckout?.total_checked_out || 0;
                const totalCheckedIn = userCheckin?.total_checked_in || 0;
                const outstandingQuantity = totalCheckedOut - totalCheckedIn;

                if (quantity > outstandingQuantity) {
                    throw new Error(`You have only ${outstandingQuantity} unit(s) to return for this product, but tried to return ${quantity}`);
                }

                newQuantity += quantity;
            } else {
                throw new Error('Invalid transaction type');
            }

            // Update product quantity
            db.prepare('UPDATE products SET quantity = ? WHERE id = ?').run(newQuantity, product_id);

            // Record transaction
            const result = db.prepare(
                'INSERT INTO transactions (product_id, user_id, type, quantity) VALUES (?, ?, ?, ?)'
            ).run(product_id, user_id, type, quantity);

            return { id: result.lastInsertRowid, product_id, user_id, type, quantity, timestamp: new Date().toISOString() };
        });

        const result = transaction();
        return NextResponse.json(result);
    } catch (error) {
        return NextResponse.json({ error: error.message || 'Transaction failed' }, { status: 400 });
    }
}
